body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  /* Remove margin and padding */
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  /* border-radius: 100px; */
}

/*/* Center the table horizontally and vertically within its container */
.table-wrapper {
  display: flex;
  justify-content: center; /* Horizontally center */
  align-items: center; /* Vertically center */
  overflow-x: auto;
  max-width: 100%;
  margin-top: -0.5rem;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  background-color: #fff;
}

/* Style the table within the wrapper */
.table-wrapper table {
  border-collapse: collapse;
  width: 100%;
  max-width: 700px;
}

/* Style table header cells */
.table-wrapper th {
  background-color: #007bff;
  color: #fff;
  padding: 10px;
  text-align: center;
}

/* Style table data cells */
.table-wrapper td {
  text-align: center;
  padding: 10px;
  border-bottom: 1px solid #ccc;
}

/* Highlight the first row (header row) with a different background */
.table-wrapper tr:first-child {
  background-color: #f2f2f2;
}

/* Add a hover effect for table rows */
.table-wrapper tr:hover {
  background-color: #f5f5f5;
}


.error,
.success {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f8d7da; /* Light red background color for errors */
  color: #721c24; /* Dark red text color for errors */
  border: 1px solid #f5c6cb; /* Light red border color for errors */
  border-radius: 5px;
  padding: 12px;
  margin-bottom: 16px;
  font-size: 16px;
  font-weight: bold; /* Make the text bold */
  text-align: center; /* Center-align the text */
  text-transform: capitalize; /* Capitalize the text (error or success) */
}
.success {
  background-color: #dff0d8; /* Light green background color for success */
  color: #228b22; /* Dark green text color for success */
  border-color: #c3e6cb; /* Light green border color for success */
}


.addbill {
  font-size: 1.5rem;
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  max-width: 300px;
  border: 3px solid rgb(17, 16, 16);
  margin-bottom: 0.5rem; /* Adjust this value */
}

label{
  margin-bottom: 5px;
  font-weight: bold;
}
input[type="data"],
input[type="number"]{
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;

}

button[type="submit"]{
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;


}

button[type="submit"]:hover{
  background-color: #0056b3;
}
h1{
  /* border: 2px solid white; */
  text-align: center;
  margin: 0.5rem 0 1rem; 
  padding: 1rem;
}
.routes {
  display: flex; /* Use flexbox to arrange links side by side */
  justify-content: center; /* Center the links horizontally */
  margin: 1.5rem;
}
.routes a {
  text-decoration: none;
  font-size: 1.2rem;
  padding: 1rem 1.5rem;
  margin: 0 1rem; /* Add some spacing between links */
  border: 1px solid #ddd;
  border-radius: 0.5rem;
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}

.routes a:hover {
  background-color: #ddd;
}

/* Style for the delete button */
.delete-button {
  background-color: #ff6347; /* Red background color */
  color: #fff; /* White text color */
  padding: 8px 12px; /* Adjust padding as needed */
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Add a smooth transition effect */

  /* You can add additional styles here, such as hover effects */
}

/* Style for the delete button on hover */
.delete-button:hover {
  background-color: #ff0000; /* Darker red on hover */
}
